-between\n  align-items: center\n  //border-bottom: 1px solid color(\"gray\", \"base\")\n  background: rgba(255,255,255,0)\n  position: relative\n  z-index: 1000\n\n// 2. TOP NAVBAR LOGO ----------------------------------------------------\n\n#wmnav-logo\n  width: 100px\n  height: auto\n  margin: $half-margin\n  position: relative\n\n  img\n    width: 100%\n    max-width: 100%\n    height: auto\n","\r\n@import './colors'; \r\n/* \r\nTABLE OF CONTENTS\r\n\r\n  0. Layout\r\n  1. Sizes\r\n  2. Typography\r\n  3. Colors\r\n  4. Spacings\r\n  5. Top Navbar\r\n  6. Buttons\r\n\r\n*/\r\n\r\n\r\n// 0. Layout\r\n// ==========================================================================\r\n\r\n$base-corners: 10px;\r\n\r\n\r\n// 1. Sizes\r\n// ==========================================================================\r\n\r\n$small-screen-up : 461px;\r\n$medium-screen-up : 782px;\r\n$large-screen-up : 1201px;\r\n\r\n$small-screen : 460px;\r\n$medium-screen : 781px;\r\n$large-screen : 1200px;\r\n\r\n$screens: (\r\n  'sm': $small-screen,\r\n  'md': $medium-screen,\r\n  'lg': $large-screen\r\n);\r\n\r\n$base-margin : calc(0.8rem + 0.5vw);\r\n$double-margin : calc(1.6rem + 1vw);\r\n$quad-margin : calc(3.2rem + 2vw);\r\n$sexta-margin : calc(4.8rem + 3vw);\r\n$okto-margin : calc(6.4rem + 4vw);\r\n$dodeca-margin : calc(9.6rem + 6vw);\r\n\r\n$half-margin : calc(0.4rem + 0.25vw);\r\n$quarter-margin : calc(0.2rem + 0.15vw);\r\n$reduced-margin : 1rem;\r\n\r\n\r\n$-quarter-margin : calc((0.2rem + 0.15vw) * -1);\r\n$-half-margin: calc((0.4rem + 0.25vw) * -1);\r\n$-base-margin : calc((0.8rem + 0.5vw) * -1);\r\n$-double-margin : calc((0.8rem + 0.5vw) * -2);\r\n$-quad-margin : calc((0.8rem + 0.5vw) * -4);\r\n$-sexta-margin : calc((0.8rem + 0.5vw) * -6);\r\n$-okto-margin : calc((0.8rem + 0.5vw) * -8);\r\n\r\n\r\n// 2. Typography\r\n// ========================================================================== \r\n\r\n\r\n//$copy-font: 'Open Sans', Helvetica, Arial, sans-serif;\r\n$copy-font: sans-serif;\r\n$headline-font: 'Exo', sans-serif;\r\n\r\n$text-max-width: 720px;\r\n\r\n$headline-color: color('gray', 'darken-3');\r\n\r\n\r\n$bread-text-sm: 16px;\r\n$bread-text-md: 18px;\r\n$bread-text-lg: 18px;\r\n\r\n$bread-text-sm-line: 26px;\r\n$bread-text-md-line: 28px;\r\n$bread-text-lg-line: 30px;\r\n\r\n$tiny-text-sm: 10px;\r\n$tiny-text-md: 11px;\r\n$tiny-text-lg: 12px;\r\n\r\n$tiny-text-sm-line: 14px;\r\n$tiny-text-md-line: 16px;\r\n$tiny-text-lg-line: 18px;\r\n\r\n$small-text-sm: 11px;\r\n$small-text-md: 12px;\r\n$small-text-lg: 14px;\r\n\r\n$small-text-sm-line: 16px;\r\n$small-text-md-line: 17px;\r\n$small-text-lg-line: 20px;\r\n\r\n$large-text-sm: 18px;\r\n$large-text-md: 22px;\r\n$large-text-lg: 24px;\r\n\r\n$large-text-sm-line: 24px;\r\n$large-text-md-line: 28px;\r\n$large-text-lg-line: 32px;\r\n\r\n$huge-text-sm: 24px;\r\n$huge-text-md: 28px;\r\n$huge-text-lg: 34px;\r\n\r\n$huge-text-sm-line: 30px;\r\n$huge-text-md-line: 34px;\r\n$huge-text-lg-line: 40px;\r\n\r\n$mega-text-sm: 40px;\r\n$mega-text-md: 52px;\r\n$mega-text-lg: 64px;\r\n\r\n$mega-text-sm-line: 50px;\r\n$mega-text-md-line: 64px;\r\n$mega-text-lg-line: 76px;\r\n\r\n$h1 : calc(2.9rem + 1vw);\r\n$h1-h2 : calc(1.75rem + 1.1vw);\r\n$h2 : calc(1.95rem + 1vw);\r\n$h3 : calc(1.56rem + 0.8vw);\r\n$h4 : calc(1.32rem + 0.4vw);\r\n$h5 : calc(1.1rem + 0.1vw);\r\n$h6 : calc(0.7rem + 0.08vw);\r\n\r\n$h1-sm: 34px;\r\n$h1-md: 40px;\r\n$h1-lg: 48px;\r\n\r\n$h2-sm: 28px;\r\n$h2-md: 34px;\r\n$h2-lg: 40px;\r\n\r\n$h3-sm: 22px;\r\n$h3-md: 25px;\r\n$h3-lg: 29px;\r\n\r\n$h4-sm: 18px;\r\n$h4-md: 22px;\r\n$h4-lg: 25px;\r\n\r\n$h5-sm: $bread-text-sm;\r\n$h5-md: $bread-text-md;\r\n$h5-lg: $bread-text-lg;\r\n\r\n$h6-sm: 15px;\r\n$h6-md: 16px;\r\n$h6-lg: 17px;\r\n\r\n\r\n$bread-text-sm: 16px;\r\n$bread-text-md: 18px;\r\n$bread-text-lg: 18px;\r\n\r\n$bread-text-sm-line: 26px;\r\n$bread-text-md-line: 28px;\r\n$bread-text-lg-line: 30px;\r\n\r\n$tiny-text-sm: 10px;\r\n$tiny-text-md: 11px;\r\n$tiny-text-lg: 12px;\r\n\r\n$tiny-text-sm-line: 14px;\r\n$tiny-text-md-line: 16px;\r\n$tiny-text-lg-line: 18px;\r\n\r\n$small-text-sm: 11px;\r\n$small-text-md: 12px;\r\n$small-text-lg: 14px;\r\n\r\n$small-text-sm-line: 16px;\r\n$small-text-md-line: 17px;\r\n$small-text-lg-line: 20px;\r\n\r\n\r\n$big-text : calc(2.5rem + 0.7vw);\r\n$bigger-text : calc(2.9rem + 0.9vw);\r\n$biggest-text : calc(3.2rem + 1.2vw);\r\n\r\n$h4-lineheight : calc(1.7rem + 0.2vw);\r\n$h4-padding : calc(1.7rem + 0.2vw) 0;\r\n\r\n$tiny-icon : calc(1.8rem + 0.4vw);\r\n$small-icon : calc(2rem + 0.5vw);\r\n$medium-icon : calc(2.6rem + 0.8vw);\r\n$big-icon : calc(4rem + 1vw);\r\n$huge-icon : calc(6rem + 1.6vw);\r\n$mega-icon: calc(8rem + 2vw);\r\n\r\n\r\n$mega-font : calc(2rem + 2vw);\r\n\r\n$lg-button: calc(4rem + 2vw);\r\n$md-button: calc(2rem + 1vw);\r\n$sm-button: calc(1rem + 0.5vw);\r\n\r\n// 3. Colors\r\n// ==========================================================================\r\n\r\n$primary-color : color(\"essblue\", \"base\");\r\n$primary-color-hover : color(\"essblue\", \"darken-3\");\r\n$primary-text : color(\"gray\", \"darken-2\");\r\n$secondary-color : color(\"gray\", \"darken-2\");\r\n$white : color(\"shades\", \"white\");\r\n\r\n//SYSTEM\r\n$success-color : color(\"green\", \"base\");\r\n$error-color : color(\"red\", \"base\");\r\n\r\n$link-color : color(\"gray\", \"base\");\r\n$ess-base-green : color(\"green\", \"base\");\r\n\r\n$card-link-color : color(\"essblue\", \"base\");\r\n$footer-font-color : color(\"gray\", \"base\");\r\n$footer-bg-color : color(\"gray\", \"lighten-4\");\r\n$footer-copyright-font-color: color(\"gray\", \"lighten-2\");\r\n$footer-copyright-bg-color : none;\r\n$ess-blue-transp : rgba(15, 42, 86, 0.2);\r\n$ess-blue-transp-hard : rgba(15, 42, 86, 0.8);\r\n\r\n\r\n\r\n\r\n// GRADIENT\r\n$ess-blue-gradient : linear-gradient(to left, color('gray', 'base'), color('gray', 'lighten-1'));\r\n$ess-blue-gradient-reverse : linear-gradient(to right, $primary-color, $secondary-color);\r\n\r\n\r\n// button colors \r\n$primary-button-bg : $primary-color;\r\n$primary-button-bg-hover : color('essblue', 'darken-1');\r\n\r\n// NAVIGATION CUBE\r\n// Current one\r\n//$nav-cube-background: radial-gradient(circle, rgba(230, 230, 230, 1) 0%, rgba(211, 211, 211, 0.85) 77%);\r\n\r\n// New one\r\n$nav-cube-background: rgba(12, 60, 146, 0.35);\r\n\r\n\r\n$border-default-color: color('gray', 'darken-2');\r\n\r\n\r\n\r\n// 4. Spacings and Sizes\r\n// ==========================================================================\r\n\r\n\r\n$spaces-map: (\r\n  \"zero\" : 0,\r\n  \"quarter\": $quarter-margin,\r\n  \"half\": $half-margin,\r\n  \"base\" : $base-margin,\r\n  \"double\" : $double-margin,\r\n  \"quad\" : $quad-margin,\r\n  \"sexta\": $sexta-margin,\r\n  \"okta\" : $okto-margin,\r\n  \"dodeca\" : $dodeca-margin,\r\n  \"minus-half\": $-half-margin,\r\n  \"minus-base\": $-base-margin,\r\n  \"minus-double\": $-double-margin,\r\n  \"minus-quad\": $-quad-margin,\r\n  \"minus-okta\": $-okto-margin,\r\n);\r\n\r\n$spaces-values: (\r\n  \"m\": (\"margin\"),\r\n  \"m-top\": (\"margin-top\"),\r\n  \"m-right\": (\"margin-right\"),\r\n  \"m-bot\": (\"margin-bottom\"),\r\n  \"m-left\": (\"margin-left\"),\r\n  \"m-hor\": (\"margin-left\", \"margin-right\"),\r\n  \"m-vert\": (\"margin-top\", \"margin-bottom\"),\r\n  \"p\":(\"padding\"),\r\n  \"p-top\": (\"padding-top\"),\r\n  \"p-right\": (\"padding-right\"),\r\n  \"p-bot\": (\"padding-bottom\"),\r\n  \"p-left\": (\"padding-left\"),\r\n  \"p-hor\": (\"padding-left\", \"padding-right\"),\r\n  \"p-vert\": (\"padding-top\", \"padding-bottom\"),\r\n);\r\n\r\n$sizes: (\r\n  \"full\": 100%,\r\n  \"auto\": auto,\r\n  \"50\": 50px,\r\n  \"100\": 100px,\r\n  \"150\": 150px,\r\n  \"200\": 200px,\r\n  \"300\": 300px,\r\n  \"400\": 400px,\r\n  \"600\": 600px,\r\n  \"700\": 700px,\r\n  \"800\": 800px,\r\n  \"1000\": 1000px,\r\n  \"1200\": 1200px,\r\n  \"1400\": 1400px,\r\n);\r\n\r\n$offsets: (\r\n  \"minus-600\": -600px,\r\n  \"minus-400\": -400px,\r\n  \"minus-300\": -300px,\r\n  \"minus-200\": -200px,\r\n  \"minus-150\": -150px,\r\n  \"minus-100\": -100px,\r\n  \"minus-50\": -50px,\r\n  \"minus-25\": -25px,\r\n  \"0\": 0px,\r\n  \"25\": 25px,\r\n  \"50\": 50px,\r\n  \"100\": 100px,\r\n  \"150\": 150px,\r\n  \"200\": 200px,\r\n  \"300\": 300px,\r\n  \"400\": 400px,\r\n  \"600\": 600px,\r\n  \"800\": 800px,\r\n  \"1000\": 800px,\r\n  \"1200\": 800px,\r\n  \"1400\": 800px\r\n);\r\n\r\n$opacities: (\r\n  \"0\": 0,\r\n  \"10\": 0.1,\r\n  \"20\": 0.2,\r\n  \"30\": 0.3,\r\n  \"40\": 0.4,\r\n  \"50\": 0.5,\r\n  \"60\": 0.6,\r\n  \"70\": 0.7,\r\n  \"80\": 0.8,\r\n  \"90\": 0.9,\r\n  \"100\":1\r\n);\r\n\r\n\r\n// 5. Top Navbar\r\n// ==========================================================================\r\n\r\n$topnav_padding: $base-margin;\r\n\r\n$nav_cube_side_width: 1.4rem;\r\n$nav_cube_side_shift: 0.9rem;\r\n$nav_cube_side_shift_2: 1rem;\r\n\r\n\r\n// 6. Blocks\r\n// ==========================================================================\r\n\r\n$block-border-radius: 10px;\r\n\r\n// 6. Buttons\r\n// ==========================================================================\r\n\r\n$button-primary: color('essblue', 'darken-1');\r\n$button-secondary: color('essblue', 'darken-2');\r\n$button-text: white;\r\n$button-text-hover: white","$csscube-h1 : calc(1rem + 1.8vw + 1.8vh + .7vmin);\n$csscube-h2 : calc(0.8rem + 0.5vw + 0.5vh + .3vmin);\n$csscube-h3 : calc(0.6rem + 0.3vw + 0.3vh + .25vmin);\n$csscube-p : calc(.4rem + .2vw + .2vh + .2vmin);\n\n$alpha-active : 0.9;\n$alpha-inactive : 0.1;\n\n\n#el-clickable-background {\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  top: 0;\n  left: 0;\n  background: $nav-cube-background;\n  //opacity: 0.99;\n}\n\n#wmnav-content {\n  display: flex;\n  flex-flow: row wrap;\n  justify-items: stretch;\n  align-items: baseline;\n  align-content: baseline;\n  z-index: -100;\n  opacity: 0;\n  position: fixed;\n  width: 100vw;\n  height: 100vh;\n  top: 0;\n  left: 0;\n\n\n}\n\n@keyframes navrot {\n  to {\n    transform: rotateY(360deg)\n  }\n}\n\n.css_block {\n\n  a {\n    padding: $half-margin;\n  }\n\n  // home banner\n\n  &.css-gray>div,\n  &.css-gray>a {\n    background: color(\"gray\", \"lighten-2\");\n\n    &:hover {\n      background: white\n    }\n  }\n\n  &.css-white>div,\n  &.css-white>a {\n    background: white;\n\n    &:hover {\n      background: white;\n      color: black;\n    }\n  }\n\n  &.css-gray-lighter>div,\n  &.css-gray-lighter>a {\n    background: color('gray', 'lighten-4');\n\n    &:hover {\n      background: white;\n    }\n  }\n\n  &.css-dark-blue>div,\n  &.css-dark-blue>a {\n    background: $secondary-color;\n  }\n\n  &.css-color-blue>div,\n  &.css-color-blue>a {\n    color: #87c8ff;\n  }\n\n  &.css-color-white>div,\n  &.css-color-white>a {\n    color: $white !important;\n\n    h2 {\n      color: $white !important;\n    }\n  }\n\n  &.css-color-dark-gray>div,\n  &.css-color-dark-gray>a {\n    color: $primary-text;\n  }\n\n\n  position: absolute;\n  overflow: hidden !important;\n}\n\n#home-banner-cube a {\npadding: 0;\n}\n#csscube-cont {\n\n  //position: relative;\n  flex-basis: 100%;\n  max-width: 100%;\n  height: 100vh;\n  align-self: baseline;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  align-self: stretch;\n\n\n  #csscube-scene {\n    z-index: 10;\n    opacity: 0;\n\n\n    position: absolute;\n    margin: auto; // centers the cube horizontaly;\n\n    #csscube-rotator {\n      transform: rotateX(-7deg);\n      z-index: 10;\n      position: relative;\n      //top: 50%;\n      transform-style: preserve-3d;\n      transform-origin: 0% 0% 50%;\n      -moz-transform-origin: 0% 0% 50%;\n      -webkit-transform-origin-z: 50%;\n\n      #csscube {\n\n        transform-style: preserve-3d;\n\n        position: relative;\n        //top: -300px;\n\n        h1 {\n          top: 0;\n          left: 0;\n          font-family: 'Exo';\n          font-weight: light;\n          line-height: 0.8;\n          text-align: left;\n        }\n\n        a {\n          .ess-anim-icon {\n            stroke: color('gray', 'darken-1')\n          }\n        }\n\n        a:hover {\n          .ess-anim-icon {\n            stroke: color('essblue', 'darken-2');\n            fill: color('essblue', 'lighten-3')\n          }\n\n        }\n\n        h2 {\n          text-align: center;\n          font-weight: normal;\n        }\n\n        .cbl-industry {\n          h2 {\n            margin-top: 0;\n            padding-bottom: $base-margin !important;\n            font-weight: normal;\n          }\n\n          a {\n            align-content: center !important;\n            padding-top: 0 !important\n          }\n\n          a:hover h2 {\n            color: black\n          }\n        }\n\n        h3 {\n          text-align: center;\n        }\n\n        h4 {\n          margin: $half-margin 0 $quarter-margin 0 !important;\n          font-weight: normal\n        }\n\n        .wmcube-side {\n\n          background: transparent;\n          position: absolute;\n          top: 0;\n          left: 0;\n          backface-visibility: visible;\n\n          .css_block {\n            display: flex;\n            align-items: stretch;\n\n            &.css-block-header {\n              text-shadow: white 1px 0 10px;\n              font-size: 120%;\n              font-weight: 700;\n              text-transform: uppercase;\n              color: white;\n\n              &>div {\n\n                &:hover {\n                  cursor: default;\n                  background-color: inherit;\n                }\n              }\n            }\n\n            &.cbl-industry {\n\n              &>a {\n                align-content: stretch;\n                padding-bottom: $base-margin;\n\n                .cc-icon-h {\n                  align-self: flex-end;\n                  width: 100%;\n                  padding: 0 25%;\n\n\n                }\n\n                h2 {\n                  align-self: flex-end;\n                }\n              }\n            }\n\n            &>div,\n            &>a {\n              clip-path: polygon(0 .3rem, 0 calc(100% - .3rem), .3rem 100%, calc(100% - .3rem) 100%, 100% calc(100% - .3rem), 100% .3rem, calc(100% - .3rem) 0, .3rem 0);\n              transform-style: preserve-3d;\n              text-decoration: none;\n              color: $secondary-color;\n              flex: 1;\n              display: flex;\n              align-items: center;\n              align-content: center;\n              justify-content: center;\n              flex-wrap: wrap;\n              position: rel ative;\n              margin: .05rem;\n              padding: $base-margin;\n              overflow: hidden;\n              transition: all .5s ease;\n\n\n              //margin: 4px;\n              &:hover {\n                text-decoration: none;\n                cursor: pointer;\n                //transform: translateY(-.5rem);\n                //scale: 1.1;\n                //background-color: $primary-color;\n                opacity: 1;\n\n              }\n\n              .icon-image {\n                padding: 0 $half-margin;\n              }\n\n              &.hover-dark:hover {\n                background: $secondary-color;\n              }\n\n              &.background-image-link {\n                img {\n                  position: absolute;\n                  top: 0;\n                  opacity: 0;\n                  transition: opacity .5s ease;\n                }\n\n                &:hover img {\n                  opacity: 1;\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n\n    #csscube-shadow {\n      background: color(\"gray\", \"base\");\n      opacity: 0.1;\n      filter: blur(30px);\n      z-index: 1;\n      transform-style: preserve-3d;\n    }\n  }\n}\n\n\n\n\n\n/** CSS CUBE NAVIGATION BUTTONS - CLOSE, LEFT AND RIGHT ---------------- **/\n\n.csscube-navbutton {\n  position: absolute;\n  wi